@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap");
@keyframes fade_in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade_in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img, picture, video, iframe, figure {
  display: block;
  max-width: 100%;
}

li {
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p, span, a, strong, blockquote, i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

::selection {
  background-color: #eb4242;
  color: #fff;
}

form, input, textarea, select, button, label {
  display: block;
  font-family: inherit;
  font-size: inherit;
  hyphens: auto;
  background-color: transparent;
  color: inherit;
}
form:focus-visible, input:focus-visible, textarea:focus-visible, select:focus-visible, button:focus-visible, label:focus-visible {
  outline: none;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  fill: currentColor;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-size: 100%;
  font-family: "Quicksand", sans-serif;
  color: #202124;
  background-color: #e9e9e9;
}
body #content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 3.75rem 0;
}

@media (min-width: 768px) {
  ::-webkit-scrollbar {
    width: 0.5rem;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #fcbbbb;
  }
  body #content {
    justify-content: center;
    align-items: center;
  }
}
.swiper {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.swiper-wrapper {
  display: flex;
  justify-content: start;
  align-items: center;
  min-height: 200px;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-pagination-bullet {
  background: #eb4242 !important;
}

.header {
  position: fixed;
  top: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  color: #fff7f7;
  background: #ef1032;
  padding: 0.5em 1em;
  width: 100%;
  height: 60px;
  z-index: 2;
}
.header svg {
  font-size: 1.5rem;
}
.header .menu {
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: center;
  position: fixed;
  top: 0;
  left: -100%;
  width: 100vw;
  height: 100%;
  padding: 1em;
  background: #eb4242;
  transition: left 200ms ease-in-out;
  z-index: 2;
}
.header .menu svg {
  align-self: start;
}
.header .menu .links {
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: center;
  gap: 0.5rem;
  text-align: center;
  margin: auto;
  padding: 0 1rem;
  overflow-y: scroll;
}
.header .menu .links .link {
  font-size: 1.25rem;
  animation: fade_in 200ms ease-in;
}
.header .menu.open {
  left: 0%;
}
.header .logo-container {
  flex-grow: 1;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.header .logo-container .logo {
  text-align: center;
}
.header .logo-container .logo h1 {
  font-size: 1.15rem;
  font-weight: 700;
  margin-bottom: -0.4rem;
}
.header .search {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  padding: 0.5em 1em;
  height: 60px;
  background: #eb4242;
  transition: right 200ms ease-in-out;
}
.header .search.open {
  right: 0%;
}
.header .search input {
  flex-grow: 1;
  background: #fff;
  color: #202124;
  padding: 0.5em 1em;
}

.card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
  background: #fff;
  box-shadow: 0 1rem 2rem 0 rgba(0, 0, 0, 0.2);
  border-radius: 2rem;
  padding: 1em;
  text-align: center;
}
.card .carrousel {
  width: 100%;
}
.card .img {
  position: relative;
  width: 100%;
}
.card .img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 1rem;
}
.card .img > div {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  font-weight: 500;
  background: #005fcb;
  color: #e6f4ff;
  border-radius: 0.5rem;
  padding: 0.25em 0.5em;
}
.card .title {
  font-size: 1.125rem;
  font-weight: 600;
}
.card .description p {
  font-size: 0.9rem;
  margin: 0.5rem 0;
}
.card .price span {
  font-size: 1.5rem;
  font-weight: 600;
  color: #cc0000;
}
.card .price .promotional {
  color: #a5a5a5;
  margin-right: 0.5rem;
  text-decoration: line-through;
}

.home-container .about {
  text-align: center;
  color: #fff;
  padding: 0.5em 1em 4em 1em;
  background: #ef1032;
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.2);
}
.home-container .about p {
  margin: 0.5rem 0;
}
.home-container .about a {
  font-size: 1.125rem;
  font-weight: 500;
}
.home-container .about svg {
  margin-left: 0.25rem;
}
.home-container .section {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  padding: 1em;
  margin-top: -4rem;
}